/*==============================================================================
# FONTS
==============================================================================*/

@font-face {
    font-family: 'Aileron';
    src: url('../fonts/aileron/aileron-bold-webfont.woff2') format('woff2'),
         url('../fonts/aileron/aileron-bold-webfont.woff') format('woff');
    font-weight: 700;
    font-style: normal;
}
@font-face {
    font-family: 'Aileron';
    src: url('../fonts/aileron/aileron-semibold-webfont.woff2') format('woff2'),
         url('../fonts/aileron/aileron-semibold-webfont.woff') format('woff');
    font-weight: 500;
    font-style: normal;
}
@font-face {
    font-family: 'Aileron';
    src: url('../fonts/aileron/aileron-light-webfont.woff2') format('woff2'),
         url('../fonts/aileron/aileron-light-webfont.woff') format('woff');
    font-weight: 300;
    font-style: normal;
}

@mixin Aileron(){
    font: {
        family: 'Aileron', sans-serif;
        weight: 300;
    }
}
