html, body {
  background-color: #111111;
  color: #FFFFFF;
  width: 100%;
  overflow-x: hidden;
  font-family: 'Aileron', sans-serif;
  font-weight: 300; }
  @media screen and (max-width: 599px) {
    html, body {
      font-size: 80%; } }

body {
  position: relative;
  min-height: 100vh; }
  @media screen and (max-width: 1360px) {
    body {
      padding: 14rem 0 0; } }

::selection {
  background: rgba(255, 255, 255, 0.05); }

*:last-child {
  margin-bottom: 0; }

/*=============================================================================
# LAYOUT
==============================================================================*/
.content-block {
  max-width: 55rem; }

#main-content {
  max-width: 65vw;
  max-width: 1100px;
  margin: 0 auto; }
  @media screen and (min-width: 1361px) {
    #main-content {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      min-width: 1000px; } }
  @media screen and (max-width: 1360px) {
    #main-content {
      max-width: 900px; } }
  @media (max-width: 1100px) {
    #main-content {
      padding-right: 0; } }

/*=============================================================================
# NAVIGATION
==============================================================================*/
#site-header {
  position: relative; }

#primary-navigation {
  position: fixed;
  left: 3rem;
  top: 3rem; }
  #primary-navigation:after {
    content: '';
    display: block;
    clear: both; }
  #primary-navigation ul {
    display: none;
    list-style: none;
    margin: 1rem 0; }
    #primary-navigation ul:hover .menu-item {
      opacity: 0.1; }
      #primary-navigation ul:hover .menu-item:hover {
        opacity: 1; }
  #primary-navigation li {
    float: left;
    clear: left; }
  #primary-navigation .menu-item {
    display: inline-block;
    padding: 1em 1em 1em 0;
    font-size: 0.75rem;
    color: #FFFFFF;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 0.5em;
    font-weight: 700;
    transition: all 0.6s; }

#socialmedia {
  position: fixed;
  right: 3rem;
  top: 3rem;
  color: rgba(255, 255, 255, 0.3);
  transition: color .3s; }
  #socialmedia span {
    line-height: 24px;
    display: block;
    float: left;
    height: 24px;
    font-weight: 500;
    letter-spacing: 1px; }
  #socialmedia a {
    margin-left: 20px;
    display: block;
    float: left;
    font-size: 0; }
  #socialmedia svg {
    fill: rgba(255, 255, 255, 0.3);
    width: 24px;
    height: auto;
    transition: fill .3s; }
    #socialmedia svg:hover {
      fill: rgba(255, 255, 255, 0.8); }
  #socialmedia:hover {
    opacity: .8; }

#sponsors {
  position: fixed;
  font-size: 0;
  right: 3rem;
  bottom: 3rem;
  transition: opacity .3s; }
  #sponsors a {
    font-size: 0; }
  #sponsors img {
    transition: opacity .3s;
    opacity: .3;
    height: 32px;
    width: auto; }
  #sponsors:hover img {
    opacity: .8; }

@media (max-width: 600px) {
  #socialmedia {
    position: relative;
    top: 0;
    left: 0;
    padding: 5rem 3rem 3rem;
    height: 2rem; }
    #socialmedia span {
      line-height: 2rem;
      height: 2rem;
      font-size: 1.75rem;
      float: none;
      margin-bottom: 2rem; }
    #socialmedia svg {
      width: 2rem; }
    #socialmedia a {
      margin-left: 0;
      margin-right: 3rem; }
  #sponsors {
    position: relative;
    left: 0;
    top: 0;
    margin: 13rem 3rem 3rem;
    height: 2rem; }
    #sponsors img {
      height: 2rem;
      position: relative;
      right: 0; } }

/*=============================================================================
# LANDING PAGE
==============================================================================*/
.landing-details {
  margin-top: 5rem; }
  .landing-details:after {
    content: '';
    display: block;
    clear: both; }
  .landing-details .event-details {
    display: block; }
    .landing-details .event-details-lg {
      font-weight: 500;
      font-size: 1.125rem;
      letter-spacing: 2px;
      display: block;
      margin-bottom: 2px; }
    .landing-details .event-details-sm {
      opacity: .7; }
  .landing-details .detailbox {
    float: left;
    margin-right: 4rem; }
    .landing-details .detailbox img {
      width: 4rem;
      display: inline-block;
      margin-right: 20px;
      vertical-align: middle;
      opacity: .9; }
    @media screen and (max-width: 1350px) {
      .landing-details .detailbox img {
        width: 3rem; } }
    @media (max-width: 1000px) {
      .landing-details .detailbox {
        float: none;
        display: block;
        clear: both;
        margin-bottom: 1.5rem;
        padding-left: 5rem;
        position: relative; }
        .landing-details .detailbox img {
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          margin: auto; } }
  .landing-details .detail-text {
    display: inline-block;
    vertical-align: middle; }

.horizon-glyph-glyph {
  background-image: url(../images/H.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat; }

.horizon-glyph {
  width: 1.75rem;
  height: 1.5rem;
  overflow: hidden;
  position: relative;
  display: inline-block; }
  .horizon-glyph .horizon-glyph-glyph {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: transform 1s;
    transform: translateY(100%); }
  .horizon-glyph.toast-is-ready .horizon-glyph-glyph {
    animation: glyphToaster 2s forwards;
    animation-iteration-count: 1; }

@keyframes glyphToaster {
  from {
    transform: translateY(100%); }
  to {
    transform: translateY(0%); } }

@media (max-width: 600px) {
  .horizon-glyph {
    opacity: .5; } }

/*=============================================================================
# HOMEPAGE POLYS
==============================================================================*/
.poly-background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  overflow: hidden; }
  .poly-background svg {
    position: absolute;
    height: auto;
    fill: none;
    stroke-linecap: square;
    stroke-miterlimit: 10;
    opacity: .8;
    min-width: 400px;
    animation-duration: 15s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    overflow: visible; }
    .poly-background svg:nth-of-type(1) {
      top: 10%;
      right: 0;
      width: 700px;
      width: 50vw;
      animation-name: svg2ColorCycle;
      transform: translate(40%, -50%); }
      .poly-background svg:nth-of-type(1) .b {
        opacity: .25; }
    .poly-background svg:nth-of-type(2) {
      top: 30%;
      left: -10%;
      width: 700px;
      width: 50vw;
      animation-name: svg1ColorCycle; }
      .poly-background svg:nth-of-type(2) .b {
        opacity: .25; }
    .poly-background svg:nth-of-type(3) {
      bottom: 0;
      right: 15%;
      width: 450px;
      width: 25vw;
      animation-name: svg3ColorCycle;
      transform: translate(0, 20%); }
      .poly-background svg:nth-of-type(3) .b {
        opacity: .25; }

@keyframes svg1ColorCycle {
  0% {
    stroke: #ff0080; }
  25% {
    stroke: yellow; }
  50% {
    stroke: #00ff80; }
  75% {
    stroke: blue; }
  100% {
    stroke: #ff0080; } }

@keyframes svg2ColorCycle {
  0% {
    stroke: #80ff00; }
  25% {
    stroke: cyan; }
  50% {
    stroke: #8000ff; }
  75% {
    stroke: red; }
  100% {
    stroke: #80ff00; } }

@keyframes svg3ColorCycle {
  0% {
    stroke: #0080ff; }
  25% {
    stroke: magenta; }
  50% {
    stroke: #ff8000; }
  75% {
    stroke: lime; }
  100% {
    stroke: #0080ff; } }
